<template>
  <div class="wrap">
    <div class="btn-wrap">
      <button class="agree" @click='handleAgree(agreeMsg)'>好</button>
      <button class="refuse" @click='handleRefuse()'>不好</button>
    </div>
    <div class="prop" v-show="isShow">
      <div class="text">{{msg}}</div>
      <div class="btn" @click="hideProp()">好呀</div>
    </div>
    <div class="mask" v-show="isShow"></div>
  </div>
</template>

<script>
export default {
  name: 'buttonGroup',
  data () {
    return {
      agreeMsg: [
        '真的吗？你答应啦？',
        '给我发消息吧，爱你～'
      ],
      refuseMsg: [
        '答应我吧',
        '工资上交',
        '家务全包',
        '房产证写你的名字',
        '保大'
      ],
      flag: 0,
      msg: '',
      isShow: false,
    }
  },
  methods: {
    handleAgree(arr) {
      let self = this;
      this.showProp(arr[0]);
      setTimeout(() => {
        self.showProp(arr[1]);
      }, 1000);
      
    },
    handleRefuse() {
      const REFUSE_LEN = this.refuseMsg.length;
      if (this.flag === REFUSE_LEN) {
        this.flag = 0;
      }
      this.showProp(this.refuseMsg[this.flag++]);
    },
    showProp(msg) {
      this.msg = msg;
      this.isShow = true;
    },
    hideProp() {
      this.isShow = false;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.btn-wrap {
  display: flex;
  justify-content: space-around;
}
button {
  padding: 8px;
  width: 100px;
  min-height: 30px;
  font-size: 14px;
  letter-spacing: 2px;
  border: none;
  box-sizing: border-box;
}
.agree {
  color: #fff;
  background: rgba(55, 183, 122, 0.9);
}
.refuse {
  color: #fff;
  background: rgba(222, 52, 52, 0.86);

}
.prop {
  z-index: 99;
  position: fixed;
  top: 35%;
  left: 50%;
  margin-left: -100px;
  width: 200px;
  height: 100px;
  background: #fff;
  border-radius: 5px;
}
.text {
  width: 100%;
  height: 65%;
  border-bottom: 1px solid #eee;
  box-sizing: border-box;
  line-height: 5;
}
.btn {
  height: 35%;
  line-height: 2;
  color: #f9db5e;
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .5);
}
</style>



